@font-face {
	font-family: 'TizTypeFont';
	src: url('./utils/fonts/WebFonts/fonts/woff/IRANSansWeb.woff') format('woff'),
		url('./utils/fonts/WebFonts/fonts/woff2/IRANSansWeb.woff2') format('woff2'),
		url('./utils/fonts/IRANSans.ttf') format('truetype');
}

* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	scroll-behavior: smooth;
}

body {
	font-family: 'TizTypeFont', sans-serif;
}

#password-helper-text {
	direction: rtl;
	text-align: right;
	margin-right: 0;
}

@media only screen and (max-width: 1370px) {
	.MuiGrid-spacing-xs-2 > .MuiGrid-item {
		padding: 5px !important;
	}
}
